import { useState } from "react";
import axios from "axios";
import Spanner from "../../component/Spanner";
export default function ContactMe() {
  const [fastName, setFastName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState();
  const [message, setMessage] = useState("");
  const [loding, setLoding] = useState(false);

  const handelsubmit = async (e) => {
    e.preventDefault();

    try {
      setLoding(true);
      const res = await axios.post("/user/recive-email", {
        lastName,
        fastName,
        phone,
        email,
        message,
      });

      if (res.data.success) {
        setLoding(false);
        Swal.fire({
          title:
            "Congratulations.......................! Your message send successfull.",
          showClass: {
            popup: "animate__animated animate__fadeInDown",
          },
          hideClass: {
            popup: "animate__animated animate__fadeOutUp",
          },
        });
      }
    } catch (error) {
      setLoding(false);
      console.log(error);
    }
  };

  return (
    <div className="spanner">
      <section id="Contact" className="contact--section">
        <div>
          <p className="sub--title">Get In Touch</p>
          <h2>Contact with Me</h2>
          <p className="text-lg">You can send a Message from here right now </p>
        </div>
        <form onSubmit={handelsubmit} className="contact--form--container">
          <div className="container">
            <label htmlFor="firstname" className="contact--label">
              <span className="text-md">First Name</span>
              <input
                onChange={(e) => setFastName(e.target.value)}
                type="text"
                className="contact--input text-md"
                name="first-name"
                id="first-name"
                value={fastName}
                required
              />
            </label>
            <label htmlFor="last-name" className="contact--label">
              <span className="text-md">Last Name</span>
              <input
                onChange={(e) => setLastName(e.target.value)}
                type="text"
                className="contact--input text-md"
                id="last-name"
                value={lastName}
                required
              />
            </label>
            <label htmlFor="email" className="contact--label">
              <span className="text-md">Email</span>
              <input
                onChange={(e) => setEmail(e.target.value)}
                value={email}
                type="email"
                className="contact--input text-md"
                name="email"
                id="email"
                required
              />
            </label>
            <label htmlFor="phone-number" className="contact--label">
              <span className="text-md">phone-number</span>
              <input
                onChange={(e) => setPhone(e.target.value)}
                value={phone}
                type="number"
                className="contact--input text-md"
                name="phone-number"
                id="phone-number"
                required
              />
            </label>
          </div>

          <label htmlFor="message" className="contact--label">
            <span className="text-md">Message</span>
            <textarea
              onChange={(e) => setMessage(e.target.value)}
              value={message}
              className="contact--input text-md"
              id="message"
              rows="8"
              placeholder="Type your message..."
            />
          </label>
          <div>{loding && <Spanner className="text-center" />}</div>
          <div>
            <button
              type="submit"
              className="btn btn-primary contact--form--btn"
            >
              Submit
            </button>
          </div>
        </form>
      </section>
    </div>
  );
}
